@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

$greenDark: #08310a;
$greenLight: #26c921;
$textColor: #222222;
$buttonCta: #24c41f;
$borderLine: #14fb93;
$sectionGrey: #f8f8f8;
$sectionGreen: linear-gradient(0deg, rgba(158,242,200,1) 50%, rgba(255,255,255,1) 100%);
$commentBg: #e6c9ec;

html {
  font-size: 62.5%;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1280px;
  @media screen and (max-width: 1280px) {
    padding: 0 16px;
  }
}
@media screen and (max-width: 1280px) {
  img {
    max-width: 100%;
  }
}


@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(38,201,33, 0.3);
  }
  100% {
    box-shadow: 0 0 0 20px rgba(38,201,33, 0);
  }
}